import React from 'react';
import { MdPlayCircleOutline } from 'react-icons/md';

export default function NextButton({ answerSimple, answerParticiple, verbDef, onTaskChange }) {
  let isTaskCompleted = answerSimple !== null && answerParticiple !== null;

  let isAnswerRight =
    answerSimple === verbDef.simple &&
    answerParticiple === verbDef.participle;

  return (
    <div className="m22-change-task d-flex flex-column align-items-end mx-4 px-5 w-100">
      <MdPlayCircleOutline
        onClick={e => {
          onTaskChange(verbDef, isAnswerRight);
        }}
        onKeyPress={e => {
          if (e.key === 'Enter') {
            onTaskChange(verbDef, isAnswerRight);
          }
        }}
        className={(isTaskCompleted ? 'active' : 'disabled') + ' m22-icon'}
      />
    </div>
  );
}
