import { shuffle } from './array';

const VOWELS_MAPPING = {
  a: ['o', 'u'],
  e: ['o', 'u'],
  i: ['ea'],
  o: ['a', 'e'],
  u: ['o', 'a']
};

export function createTask(verbDef) {
  const simple = createVerb(verbDef.base, verbDef.simple);
  const participle = createVerb(verbDef.base, verbDef.participle, simple);

  return {
    base: verbDef.base,
    simple,
    participle,
    def: verbDef
  };
}

function createVerb(base, verb, simple) {
  let firstVerb = changeVowels(verb);
  let secondVerb = simple
    ? simple.some(variant => !!variant.match(/ed$/))
      ? createEd(base)
      : changeVowels(verb)
    : changeVowels(verb);

  if (firstVerb.match(/[aieou].*/) !== null) {
    while (firstVerb === verb) {
      firstVerb = changeVowels(firstVerb);
    }
  }
  if (secondVerb.match(/[aieou].*/) !== null) {
    while (secondVerb === firstVerb || secondVerb === verb) {
      secondVerb = changeVowels(secondVerb);
    }
  }

  return shuffle([verb, firstVerb, secondVerb]);
}

function createEd(verb) {
  return verb + 'ed';
}

function flipCoin() {
  return Math.random() > 0.5;
}

function changeVowels(word) {
  const letters = word.split('');
  let result = '';

  let changed = false;

  for (let letter of letters) {
    if (!!VOWELS_MAPPING[letter]) {
      if (!changed || flipCoin()) {
        result += shuffle(VOWELS_MAPPING[letter])[0];
        changed = true;
      } else {
        result += letter;
      }
    } else {
      result += letter;
    }
  }

  return result;
}

window.changeVowels = changeVowels;
