export const CHANGE_TASK = 'CHANGE_TASK';
export const REPLAY_GAME = 'REPLAY_GAME';

export function changeTask(verbDef, isTaskRight) {
  return {
    type: CHANGE_TASK,
    payload: { verbDef: verbDef, isTaskRight: isTaskRight }
  };
}

export function replayGame() {
  return {
    type: REPLAY_GAME
  };
}
