import React from 'react';
import { Button } from 'react-bootstrap';

export default function Answer({ verbForm, selectedAnswer, rightAnswer, variants, onAnswerClick }) {
  const answers = variants.map((variant, idx) => {
    return {
      key: variant + idx,
      className:
        selectedAnswer === variant
          ? variant === rightAnswer
            ? 'm22-correct'
            : 'm22-fail'
          : !!selectedAnswer && variant === rightAnswer
          ? 'm22-correct'
          : '',
      verb: variant
    };
  });

  return (
    <div className={'m22-' + verbForm + ' m22-answer-block d-flex flex-column w-100'}>
      <h6 className="text-right font-italic">{verbForm}</h6>
      {answers.map((answer, idx) => (
        <Button
          variant="secondary"
          onClick={e => onAnswerClick(answer.verb, verbForm)}
          className={answer.className + ' mb-3 mb-sm-4'}
          key={answer.key}
        >
          {answer.verb}
        </Button>
      ))}
    </div>
  );
}
