import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../store/game/actions';
import Task from './Task';
import ReplayButton from './ReplayButton';

function Game({ incoming, handleReplayClick }) {
  return (
    <main className="d-flex flex-column flex-fill ">
      {incoming.length ? <Task /> : <ReplayButton onReplayClick={handleReplayClick} />}
    </main>
  );
}

const mapStateToProps = state => {
  return {
    incoming: state.game.incoming
  };
};

const mapDispatchToProps = {
  handleReplayClick: actions.replayGame
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Game);
