import React from 'react';

import { MdErrorOutline } from 'react-icons/md';

export default function Progress({incoming, finished, failed}) {
  return (
    <div className="m22-tast-progress d-flex px-5 w-100 justify-content-between align-items-center">
      <div className="m22-fail-count d-flex">
        <div>
          <MdErrorOutline className="m22-icon" />
        </div>{' '}
        <div className="fail-text">{failed}</div>
      </div>
      <div>{finished.length + '/' + (incoming.length + finished.length)}</div>
    </div>
  );
}
