import { CHANGE_TASK, REPLAY_GAME } from './actions';
import { shuffle } from '../../utils/array';

const raw = require('../../data/verbs.json');
const verbs = shuffle(raw.data);

const fails = !!localStorage.getItem('VERBER_FAILED_TASKS') ?
  JSON.parse(localStorage.getItem('VERBER_FAILED_TASKS'))
: []

const initialState = {
  current: verbs[0],
  finished: [],
  fails: fails,
  currentFailedCount: 0,
  incoming: verbs.slice(1, verbs.length),
  status: 'bla'
};

export default function gameReducer(state = initialState, action) {
  switch (action.type) {
    case CHANGE_TASK:
      const { verbDef, isTaskRight } = action.payload;

      return {
        finished: [state.current, ...state.finished],
        current: state.incoming.length > 0 ? state.incoming[0] : null,
        incoming: state.incoming.slice(1, state.incoming.length),
        status: state.incoming.length === 0 ? 'You win' : null,
        fails: isTaskRight ? state.fails : [...state.fails, verbDef],
        currentFailedCount: isTaskRight ? state.currentFailedCount : state.currentFailedCount + 1
      };
    case REPLAY_GAME:
      return initialState;
    default:
      return state;
  }
}
