import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import * as actions from '../store/game/actions';
import { createTask } from '../utils/game';
import Answer from './Answer';
import Progress from './Progress';
import NextButton from './NextButton';

function makeState(verbDef) {
  return {
    ...createTask(verbDef),
    answerSimple: null,
    answerParticiple: null
  };
}

function Task({ current, finished, incoming, currentFailedCount, handleTaskChange }) {
  const [taskState, setTaskState] = useState(makeState(current));

  const handleAnswerClick = (variant, verbForm) => {
    const answerKey = 'answer' + verbForm.charAt(0).toUpperCase() + verbForm.slice(1);
    if (taskState[answerKey]) return;

    setTaskState({
      ...taskState,
      [answerKey]: variant
    });
  };

  useEffect(() => {
    setTaskState(makeState(current));
  }, [current]);

  return (
    <div className="m22-task d-flex flex-column flex-fill align-items-center">
      <div className="m22-base w-100">{taskState.base}</div>
      <Progress incoming={incoming} finished={finished} failed={currentFailedCount} />
      <Answer
        verbForm="simple"
        selectedAnswer={taskState.answerSimple}
        rightAnswer={taskState.def.simple}
        variants={taskState.simple}
        onAnswerClick={handleAnswerClick}
      />
      <Answer
        verbForm="participle"
        selectedAnswer={taskState.answerParticiple}
        rightAnswer={taskState.def.participle}
        variants={taskState.participle}
        onAnswerClick={handleAnswerClick}
      />
      <NextButton
        answerSimple={taskState.answerSimple}
        answerParticiple={taskState.answerParticiple}
        verbDef={taskState.def}
        onTaskChange={handleTaskChange}
      />
    </div>
  );
}

const mapStateToProps = state => {
  return {
    current: state.game.current,
    finished: state.game.finished,
    incoming: state.game.incoming,
    status: state.game.status,
    currentFailedCount: state.game.currentFailedCount
  };
};

const mapDispatchToProps = {
  handleTaskChange: actions.changeTask
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Task);
